.r-loader-container {
  position: absolute;
  width: 6rem;
  height: 6rem;
}
.r-loader__label {
  color: white;
  position: absolute;
  width: 11rem;
  left: 50%;
  transform: translate(-28%, 0);
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 100%;
  height: 100%;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes lds-ripple {
  0% {
    top: 3rem;
    left: 3rem;
    width: 0;
    height: 0;
    opacity: 0;
  }
  4.9% {
    top: 3rem;
    left: 3rem;
    width: 0;
    height: 0;
    opacity: 0;
  }
  5% {
    top: 3rem;
    left: 3rem;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 6rem;
    height: 6rem;
    opacity: 0;
  }
}